import React from "react";
import "@fontsource/anton";
import {
  Stack,
  Text,
  Box,
  HStack,
  VStack,
  Heading,
  Spacer,
  Flex,
  useMediaQuery,
  Button,
  useColorMode,
  useColorModeValue,
  Link
} from "@chakra-ui/react";
import { motion } from "framer-motion";
import martian_planet from "../../components/img/martian_planet.png";
import useWindowDimensions from "../../components/ui-components/ScreenHW";
import AboutUs from "./AboutUs";
import AboutUsExe from "./AboutUsExe";
import Metaverse from "./Metaverse";
import Finance from "./Finance";
import MarsExploration from "./MarsExploration";
import MarsRotation from "./MarsRotation";
import RoadMap from "./RoadMap";
import FindUs from "./FindUs";
import Footer from "../../components/ui-components/Footer";

const MotionBox = motion(Box);
const MotionHStack = motion(HStack)
const MotionVStack = motion(VStack)


function Home() {
  const [isPC] = useMediaQuery("(min-width: 750px)");
  const { colorMode } = useColorMode();
  const { height, width } = useWindowDimensions();
  return (
    <MotionVStack
    initial={{
      opacity: 0,
    }} 
      animate={{
        opacity: 1

      }}
      transition={{
        duration: 1
      }}
      top="0"

      
     
     
      
    
    >
    <MarsRotation/>
    <Box w="100%" h="100vh" position="absolute" boxShadow="xl">
    <MotionHStack
        
          w="100%"
          h="100vh"
          
        
          
         
          justify="center"
          backgroundImage={martian_planet}
          bgSize={isPC ? "700px" : "500px"}
          
          backgroundPosition={isPC ? "right bottom" : "center top"}
          backgroundRepeat="no-repeat"
          
          
          
        >
          <VStack
            w="85%"
            pt={isPC ? "" : "30vh"}
            justify="center"
            align={isPC ? "flex-start" : "center"}
          >
            <VStack
              p={isPC ? "50px" : "5px"}
              bgColor={isPC ? "" : colorMode == "light" ? "white" : "gray.800"}
              borderRadius="25px"
              spacing={isPC ? "0px" : "-5px"}
              align={isPC ? "flex-start" : "center"}
              
              w="100%"
            >
              <Text align={isPC ? "left" : "center"} >
                <Heading as="h6">Welcome</Heading>
                <Heading
                  
                  size="4xl"
                  
                  fontFamily="anton"
                  fontWeight="thin"
                  bgGradient='linear(to-l, #7928CA, #FF0080)' 
                  bgClip='text'
                 
                  
                  
                  
                  
                >
                  To The Mars
                </Heading>
                
                <Heading as="h1">
                  <Text as="inline" color="twitter.500">
                    #
                  </Text>
                  Metaverse{" "}
                  <Text as="inline" color="twitter.500">
                    #
                  </Text>
                  Finance
                </Heading>
                <Heading as="h1">
                  <Text as="inline" color="twitter.500">
                    #
                  </Text>
                  MarsExplorationPrograms
                </Heading>
              </Text>

              <HStack spacing="10px" pt="10px">
      <VStack w="100%" d="flex" alignItems="center" justifyContent="center">
       <Link href="https://app.tothemars.in" style={{textDecorationLine: "none"}}  isExternal >
       <MotionBox
         as="button"
         
         
         whileHover={{
           scale: 1.02,
           
           
           
           
        }}

         whileTap= {{
           scale: 0.97,
         }}
         padding="3"
         paddingLeft="10"
         paddingRight="10"
         borderRadius="10px"
         bgColor={colorMode == "light" ? "twitter.500" : ""}
         border={colorMode == "light" ? "" : "1px"}
         marginLeft={isPC ? "10px" : ""}
         shadow="2xl"
         
         
         
         size="lg"
         display="flex"
         color="white"
       >
         <Heading as="h6" size="md">
          
                    Let's invest in the future
                   
                  </Heading>
         </MotionBox> </Link>
       </VStack>
               
              </HStack>
            </VStack>
          </VStack>
        </MotionHStack>

    </Box>

    
    <Box w="100%" >
      <Stack direction="column" w="100%" align="center">
        
      </Stack>
      <AboutUs />
      <Metaverse />
      <Finance />
      <MarsExploration/>
      <RoadMap/>
      
      <FindUs/>
      {/* <Mission/> */}
      
    
    </Box>
    </MotionVStack>
  );
}

export default Home;
