import React, { StrictMode, Suspense } from "react";
import ReactDOM from "react-dom";
import { ChakraProvider, ColorModeScript, extendTheme, CSSReset, VStack, Text } from "@chakra-ui/react";
import { DAppProvider, ChainId } from "@usedapp/core";
import {BrowserRouter, Routes, HashRouter} from "react-router-dom"


import App from "./App";



const configT =  {
  initialColorMode: "dark",
  useSystemColorMode: false,

}

const config = {
  readOnlyChainId: ChainId.BSC,
 
  readOnlyUrls: {
    
    [ChainId.BSC]: "https://bsc-dataseed.binance.org/",
  },
}
// 3. extend the theme
const theme = extendTheme({ configT })

const rootElement = document.getElementById("root");
ReactDOM.render(
  
  <DAppProvider config = {config}>
  
  <StrictMode>
    <ChakraProvider config={configT}>
    <ColorModeScript initialColorMode={theme.config.initialColorMode} />
      <CSSReset/>
      <HashRouter>
      
      <App />
      
      </HashRouter>
    </ChakraProvider>
  </StrictMode>
  
  </DAppProvider>
  
  ,
  rootElement
);
