import React, {Suspense, useRef} from "react";
import {Canvas, useFrame, useLoader } from "react-three-fiber";
import {OrbitControls, Stars} from "@react-three/drei";
import { Stack, VStack, HStack, Text, Box, useMediaQuery} from "@chakra-ui/react";
import MarsMapNormal from "../../components/img/Mars/MarsNormal.jpg";
import MarsMapMain from "../../components/img/Mars/MarsMain.jpg"
import MarsMapSpecular from "../../components/img/Mars/MarsSpecular.jpg"
import MarsMapBump from "../../components/img/Mars/MarsBump.jpg"
import {TextureLoader} from "three";
import * as THREE from "three"



function Mars(props) {
    const [MarsNormal, MarsMain, MarsSpecular, MarsBump] = useLoader(TextureLoader, [MarsMapNormal, MarsMapMain, MarsMapSpecular, MarsMapBump])
    const MarsRef = useRef();
    const [isPC] = useMediaQuery("(min-width: 750px)")
    
    useFrame(({clock}) => {
        const elapsedTime = clock.getElapsedTime();
        MarsRef.current.rotation.y = elapsedTime / 10;
        


    });

    
    return (
        <>
        {isPC ? <mesh ref={MarsRef}>
            <sphereGeometry args={[2.7, 35, 35]}/>
            <meshPhongMaterial />
            <meshBasicMaterial map={MarsNormal}  metalness={5} roughness={0.7}/>

        </mesh> :
       
       
        <mesh ref={MarsRef}>
            <sphereGeometry args={[1.8, 35, 35]}/>
            <meshPhongMaterial specularMap={MarsSpecular}/>
            <meshBasicMaterial map={MarsNormal} normalMap={MarsBump} metalness={0.4} roughness={0.7}/>

        </mesh>}
        </>
    );
}

export default function MarsRotation() {
    const [isPC] = useMediaQuery("(min-width: 750px)")

    
    return (
       
        <VStack w="100%" h="100vh" align={isPC ? "flex-end" : ""}>
            <Box w={isPC ? "700px" : "100%"} h="100vh" marginTop={isPC ? "10vh" : "-10vh"}>
           <Canvas>
               
               
               
             
               
               <Mars/>
              
               
           </Canvas>
           </Box>

        </VStack>
        

    )
}