import React, {useState, useEffect} from "react";
import {
  Stack,
  Box,
  useMediaQuery,
  useColorMode,
  Link,
  Text,
  border
} from "@chakra-ui/react";
import {motion} from "framer-motion"
import {Link as RouterLink, useLocation} from "react-router-dom"
import { Box2 } from "three";
import ConnectButton from "./ConnectButton";

const MotionBox = motion(Box)

function NavMenu(props) {
  const [isPC] = useMediaQuery("(min-width: 750px)");
  const { colorMode, toggleColorMode } = useColorMode();
  const {pathname} = useLocation();
    
  

 
const NavMenu = (props) => {

  

  return(

    <MotionBox
    cursor="pointer"
    _hover={{
      color: "rgb(255,69,0)",
    }}
  >
       <Link style={{
         textDecorationLine: "none",
        

         
       }} as={RouterLink} to={props.link} onClick={props.click}> {props.name}  </Link>
      
       
  </MotionBox>
   

  );

}

  return (
    <Stack
      direction={isPC ? "row" : "column"}
      spacing={isPC ? "35px" : ""}
      fontWeight="medium"
      fontSize={isPC ? "large" : "2xl"}
      justify="center"
      align="center"
    >
      
      <NavMenu name="Home" link="/" click={props.click} />
      <NavMenu name="Martianomics" link="/tokenomics" click={props.click} />
      <NavMenu name="Team" link="/Team" click={props.click} />
      {pathname != "/MartianSwap" ? 
      <NavMenu name="Be an Early Believer" link="/MartianSwap" click={props.click} />

      :

      <ConnectButton/> }
      {/* <NavMenu name="How it works?" link="/"/> */}
      {/* <Link
          style={{
            textDecorationLine: "none",
          }}
         
          onClick={props.click}
          href="https://app.tothemars.in"
          isExternal
          
        >
      <Box
        cursor="pointer"
        _hover={{
          color: "rgb(255,69,0)",
          
        }}
       
      >
       
         
         <Text >Buy Now</Text>
        
      </Box>
      </Link> */}
    </Stack>
  );
}

export default NavMenu;
