import { Suspense, useEffect } from "react";

import NavHome from "./components/ui-components/NavHome";
import Nav from "./components/ui-components/Nav";
import MartianSwapPage from "./pages/MartianSwapPage";
import Team from "./pages/Team";
import { VStack, Text, Stack , Image, Box, Center} from "@chakra-ui/react";
import Home from "./pages/Home/";
import Tokenomics from "./pages/Tokenomics";
import useScrollOnRouteChange from "./hooks/useScrollOnRouteChange"

import {
  Routes,
  HashRouter,
  
  Route,
  useLocation
  
  
} from "react-router-dom";

import Footer from "./components/ui-components/Footer";
import logotothemars_trans from "./components/img/logo_round_large.png";

export default function App() {
  const {pathname} = useLocation();
  useEffect(() => {
    setTimeout(() => {
      window.scroll({
        top: 0,
        left: 0,
        behavior: 'smooth',
      })
    }, 50)




  }, [pathname]);
  useScrollOnRouteChange()
  
  return (
    <Suspense fallback={<VStack w="100%" h="100vh" align="center" justify="center"><Box bgColor="black" borderRadius="50%">
    <Image
      src={logotothemars_trans}
      href="www.tothemars.in"
      boxSize="100px"
      objectFit="cover"
     
    ></Image>
  </Box><Text>Loading...</Text></VStack>}>
  {/* <VStack w="100%" h="100vh" align="center" justify="center"><Box bgColor="black"  borderRadius="50%">
    <Image
      src={logotothemars_trans}
      href="www.tothemars.in"
      boxSize="100px"
      objectFit="cover"
     
    ></Image>
  </Box><Text>Loading...</Text></VStack> */}
  
      <NavHome/> 
     
      
      

      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/Tokenomics" element={<Tokenomics />} />
        <Route path="/MartianSwap" element={<MartianSwapPage />} />
        <Route path="/Team" element={<Team />} />
        <Route element={<Home />} />
      </Routes>
     
      <Footer />
    </Suspense>
  );
}
