import React from "react";
import ConnectButton from "./ConnectButton";
import {BsJustify} from "react-icons/bs"
import {FaTelegram} from "react-icons/fa"
import {AiFillTwitterCircle, AiFillInstagram} from "react-icons/ai"
import {
  HStack,
  Spacer,
  Icon,
  Image,
  Box,
  useMediaQuery,
  useColorMode,
  Flex,
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  useDisclosure,
  Button,
  VStack,
  Text,
  Heading,
  Link
} from "@chakra-ui/react";
import { AiFillHome } from "react-icons/ai";
import ColorModeChanger from "./ColorModeChanger";
import logotothemars_black from "../img/logo_round_large.png";
import logotothemars_trans from "../img/logo_round_large_t.png";
import NavMenu from "./NavMenu";
import {Link as RouterLink} from "react-router-dom"

function NavHome() {
  const [isPC] = useMediaQuery("(min-width: 750px)");
  const { colorMode, toggleColorMode } = useColorMode();
  const {isOpen, onOpen, onClose} = useDisclosure();
  const btnRef = React.useRef();
  return (
    <HStack
      width="100%"
      h="80px"
     
      p="7px 5%"
      minWidth="350px"
      zIndex="11"
      boxShadow="base"
      position="fixed"
      bgColor={colorMode == "light" ? "white" : "gray.800" }
      opacity="0.97"
      
      
    >
      <Link as={RouterLink} to="/">
      <Box bgColor={colorMode == "light" ? "black" : ""}  borderRadius="50%" cursor="pointer">
        <Image
          src={logotothemars_trans}
          href="www.tothemars.in"
          boxSize="70px"
          objectFit="cover"
         
        ></Image>
      </Box></Link>
      <Spacer />
     { isPC ?

     <NavMenu/> : <BsJustify size="30px" cursor="pointer" ref={btnRef} onClick={onOpen}/> }
      <Drawer
        isOpen={isOpen}
        placement='right'
        onClose={onClose}
        finalFocusRef={btnRef}
      >
        <DrawerOverlay />
        <DrawerContent>
          <DrawerCloseButton />
          {/* <DrawerHeader>Create your account</DrawerHeader> */}

          <DrawerBody>
            <VStack h="70vh" justify="center">
              <NavMenu click={onClose}/>
            </VStack>
            
          </DrawerBody>

          <DrawerFooter>
            <HStack w="100%" justify="center">
              <Link href="https://www.twitter.com/letsToTheMars" isExternal onClick={onClose}>
              <Icon as={AiFillTwitterCircle} boxSize="10"/>
              </Link>
              <Link href="https://t.me/letsToTheMars" isExternal onClick={onClose}>
              <Icon as={FaTelegram} boxSize="10"/>
              </Link>
              <Link href="https://www.instagram.com/letsToTheMars" isExternal onClick={onClose}>
              <Icon as={AiFillInstagram} boxSize="10"/>
              </Link>
              

            </HStack>
            
          </DrawerFooter>
        </DrawerContent>
      </Drawer>

      <ColorModeChanger />
    </HStack> 

    
  );
}

export default NavHome;
