import React from "react";
import {
  Stack,
  Heading,
  Box,
  VStack,
  HStack,
  Flex,
  Image,
  Text,
  Center,
  useColorMode,
  useColorModeValue,
  useMediaQuery,
  useMenuDescendantsContext,
  color
} from "@chakra-ui/react";
import "@fontsource/anton";
import mars_habitable from "../../components/img/mars_habitable.png"
import { Canvas } from "react-three-fiber";
import {OrbitControls, Stars} from "@react-three/drei"
import StarsBackground from "./StarsBackground"


export default function MarsExploration() {
  const { colorMode } = useColorMode();
  const [isPC] = useMediaQuery("(min-width: 750px)")
  return (
    <>
    <StarsBackground/>
   
    <Flex position="absolute" marginTop="-100vh"> 
      
      
       
    <VStack
      pt="100px"
      w="100%"
      
      w="100%"
      h="100vh"
     
      
      backgroundImage={mars_habitable}
      backgroundRepeat="no-repeat"
      backgroundPosition="center bottom"
      backgroundSize={isPC ? "40%" : "150%"}
      
      boxShadow="xl"
     
     
      
     
    >
     
      
      
      
      
      
      <HStack tag="Heading" spacing="0px">
      <Heading align="center" color="twitter.500">
        MARS EXPLORATION PROGRAM
      </Heading>   
      </HStack>
      
      <Stack w="100%"  direction={isPC ? "row" : "column"} spacing={isPC ? "50px" : "10px"}  justify={isPC ? "center" : "flex-start"} align="center" >
        <Stack direction={isPC ? "row" : "column"}  justify="center" align="center">
          <Heading pt={isPC ? "" : "20px"} minW={isPC ? "650px" : "300px"} maxW={isPC ? "" : "300px"}  align="center" size="lg" maxW={isPC ? "50%" : "90%"}>Our long term goal to conduct a series of Mars Exploration Programs to make Mars habitable for us one day. We have created a Mars Mission Reserve for this.</Heading>
          {/* <Image src={martian} boxSize="lg" h="400px" objectFit="contain"></Image> */}
        </Stack>
          

      </Stack>
      
      
    </VStack>
     
    </Flex>
    </>
    
  );
}
